body {
  max-width: 100%;
  width: 100%;
}

.contact-whole {
  min-height: 82vh;
  max-width: 100%;
}

.map-container {
  min-height: 41vh;
  background-color: burlywood;
}

.contact-info-container {
  min-height: 60vh;
  background-color: rgb(50, 63, 63);
  padding: 0;
  margin: 0;
}

.contact-form-container {
  min-height: 101vh;
  background-color: rgb(182, 182, 182);
  padding: 0;
  margin: 0;
}

.contact-text {
  color: white;
}

.buttonContactUs {
  width: 500;
}

.jumbotron-contact-page-googlemap {
  margin-bottom: 50px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
}

.hero-image-desktop-version-contact-page-googlemap {
  height: 20rem;
    padding-bottom: 15vh;
}

.hero-image-mobile-version-contact-page-googlemap {
  height: 15rem;
}

.form-wrapper-new {
  position: relative;
}

.contact-form-new {
  background-color: #eeeeee;
}

.contact-form-wrapper-inner {
  background-color: #f1f1f1;
  padding: 1rem;
}

.contact-form-wrapper-outer-outer {
  margin-top: 15rem;
  margin-bottom: 15rem;
}

.fa {
  padding: 20px;
  font-size: 30px;
  width: 50px;
  text-align: center;
  text-decoration: none;
  margin: 5px 2px;
  border-radius: 50%;
}

.fa:hover {
    opacity: 0.7;
}

.fa-facebook {
  background: #3B5998;
  color: white;
}

.fa-twitter {
  background: #55ACEE;
  color: white;
}

.fa-google {
  background: #dd4b39;
  color: white;
}

.fa-linkedin {
  background: #007bb5;
  color: white;
}

.fa-youtube {
  background: #bb0000;
  color: white;
}

.fa-instagram {
  background: #125688;
  color: white;
}

.fa-pinterest {
  background: #cb2027;
  color: white;
}

.fa-snapchat-ghost {
  background: #fffc00;
  color: white;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

.fa-skype {
  background: #00aff0;
  color: white;
}

.fa-android {
  background: #a4c639;
  color: white;
}

.fa-dribbble {
  background: #ea4c89;
  color: white;
}

.fa-vimeo {
  background: #45bbff;
  color: white;
}

.fa-tumblr {
  background: #2c4762;
  color: white;
}

.fa-vine {
  background: #00b489;
  color: white;
}

.fa-foursquare {
  background: #45bbff;
  color: white;
}

.fa-stumbleupon {
  background: #eb4924;
  color: white;
}

.fa-flickr {
  background: #f40083;
  color: white;
}

.fa-yahoo {
  background: #430297;
  color: white;
}

.fa-soundcloud {
  background: #ff5500;
  color: white;
}

.fa-reddit {
  background: #ff5700;
  color: white;
}

.fa-rss {
  background: #ff6600;
  color: white;
}

.fa-phone {
  background: #2c6d3a;
  color: white;
}

